<style scoped>
  .panel {
    height: 35px;
    width: 100%;
    background-color: #f2f2f3;
    padding-top: 10px;
  }

  .version {
    color: rgb(64, 158, 255);
  }

  .version-label {
    float: right;
    padding-right: 256px;
  }

  .language-label {
    float: left;
    padding-left: 323px;
  }
</style>



