<template>
  <div class="panel">
    <el-row>
      <h1>Welcome to contribute to Tigo!</h1>
      <div class="content">Tigo is a open source framework, welcome to contribute to Tigo. Please glance README before commit code.</div>
      <div class="content">You should fork the project firstly, then make a PULL request, we will merge your codes if accepted.</div>
    </el-row>
    <el-row>
      <img src="../../assets/contribute.png"/>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: "DocumentationContribute"
  }
</script>

<style scoped>
  .panel {
    height: 450px;
    width: 100%;
    background-color: #f0f0f1;
  }

  h1 {
    margin-top: 30px;
  }

  .content {
    color: #797e8e;
    font-family: "Avenir", serif;
  }
</style>



