import Vue from 'vue'
import Element from 'element-ui'
import Router from 'vue-router'
import DocumentationEn from '@/components/DocumentationEn'
import DocumentationCh from '@/components/DocumentationCh'

Vue.use(Router);
Vue.use(Element);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/Tigo',
      name: 'DocumentationCh',
      component: DocumentationCh
    },
    {
      path: '/Tigo/en',
      name: 'DocumentationEn',
      component: DocumentationEn
    },
  ]
})



